@mixin default-font($deffont) {
  font-family: Roboto;
  font-size: $deffont; }

a {
  text-decoration: none; }

h1 {
  @include default-font(35px);
  line-height: 1.166666667;
  font-weight: bold;
  color: rgb(51, 51, 51);
  margin: 23px 0 0 0; }

h3 {
  @include default-font(1rem);
  color: rgb(102, 102, 102);
  font-weight: 400;
  margin-top: 18px;
  margin-bottom: .9375rem; }

